<template>
  <div class="agreement">
    <mt-header fixed>
      <router-link to="/lottery" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <agreement :agreement="agreement"></agreement>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Agreement from '@/components/agreement'
export default {
  data () {
    return {
    }
  },
  computed: {
    ...mapState({
      agreement: states => states.agreement
    })
  },
  components: {
    Agreement
  }
}
</script>

<style lang="scss" scoped>
.agreement {
  position: relative;
  height: 100%;
}
</style>



